.mobile-header-wrap {
  display: flex;
  justify-content: space-between;
  .mobile-connect-btn, .mobile-connected {
    padding: 6px 11px;
    border-radius: 15px;
    font-size: 12px;
    line-height: 17px;
    font-weight: 600;
    cursor: pointer;
  }
  .mobile-connect-btn {
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
  }
  .mobile-connected {
    background: rgba(90,20,225,0.2);
    border: 1px solid rgba(193,160,255,0.31);
    backdrop-filter: blur(2px);
    color: #C1A0FF;
  }
}